body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background: linear-gradient(to right, #141e30, #243b55);
  color: #e0e1dd;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.card-container{
    background-color: #415a77;
    display:inline-block;
    border-radius:.5rem;
    padding:1rem;
    margin:1rem;
    -webkit-transform:translateZ(0);
            transform:translateZ(0);
    transition:-webkit-transform 0.25s ease-out;
    transition:transform 0.25s ease-out;
    transition:transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
    border-width:.25rem;
    box-shadow:4px 4px 8px 0px rgba( 0, 0, 0, 0.2 );
    text-align: center;
  }
  
  .card-container:hover{
    -webkit-transform:scale(1.05);
            transform:scale(1.05);
  }
.search-bar{
    padding:1em;
  }

input{
    padding: 1em;
    background-color: #e0e1dd;
    border: none;
    border-radius: .5rem;
}

@font-face {
    font-family: 'oriental_catslight';
    src: url(/cat-generator/static/media/orientalcatslight-2ozb8-webfont.23fdefde.woff2) format('woff2'),
         url(/cat-generator/static/media/orientalcatslight-2ozb8-webfont.8bbfb704.woff) format('woff');
    font-weight: normal;
    font-style: normal;

}

h1{
    font-family: oriental_catslight;
    font-size: 4em;
    font-weight: 100;
}
